
* {
  font-family: Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  width: 100%;
  font-size: 1.3rem;
  color: darkblue;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin: 10px;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 175px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}